import React from "react";
import Footer from "../Footer";
import Navbar from "../navbar";

const DistrictMsg = () => {
  return (
    <>
      <Navbar />

      <section>
        <div className="container">
          <h1 className="main-heading text-center mt-5 fw-bold">
            Rtn. Dr. Dushyant Choudhary (District Gov)
          </h1>
          <p className="text-start main-hero-para mb-0 msg-para">
            Dear President Raman Wazir and his team members,
          </p>
          <p className="text-start main-hero-para msg-para">
            It is my pleasure to congratulate each of you for you for stepping
            up to continue serving your esteemed rotary club Jammu Tawi in the
            coming year 2021-22. Change is why we are here, and what we strive
            for. We see the world not as it is but as it might be; and we
            believe, that we must be the change we wish to see in the world. I
            am confident and sure that you along with your team will have a busy
            and rewarding year.
            <br />
            <br />
            We all know that the name of Rotary is attached to countless
            projects every year, from blood banks to food banks, school
            sanitation to polio eradication. One hundred thirteen years after
            the first Rotary club was founded, Rotary service reaches literally
            around the globe. Our Club has Dynamic and passionate members who
            has Great determination to work on Rotary ideology. However, while
            being innovative in adapting to the current reality and looking to
            the future it is important that we look back at the rich history of
            the Rotary Club Jammu Tawi. I am sure that under your dynamic
            leadership, we will relive our past ,by hearing from our senior
            members tell us what the club was like 30 or 40 years ago and to
            also ensure that some of the outstanding contributions we have made
            to our city are not forgotten.
          </p>
          <p className="text-start main-hero-para mt-3 msg-para">
            I look forward to a challenging and very productive year for our
            club.
          </p>
        </div>
      </section>

      <Footer />
    </>
  );
};

export default DistrictMsg;
